.container {
    list-style-type: none;
    display: inline-block;
    width: 100%;
    box-sizing: border-box;
    margin: 0px;
    padding: 10px;
}

.layoutHead {
    padding: 10px 21px;
    background-color: #f3f4f5;
    color: #4B5966;
    position: -webkit-sticky;
    position: sticky;
    top: -20px;
    z-index: 9;
}

.layoutHeading {
    font-size: 20px;
    margin: 0px;
    font-weight: 400;
}

.container li {
    float: left;
    width: 32.3%;
    margin: 0px 1.5% 20px 0;
    padding: 10px 10px;
    cursor: pointer;
    box-sizing: border-box;
    /* border-radius: 4px; */
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.05);
}

.container li:nth-child(3n) {
    margin-right: 0px;
}

.closeLayoutOption {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    padding: 10px 21px;
    background-color: #f3f4f5;
    font-size: 24px;
    cursor: pointer;
}

.layoutCol {
    /* float: left; */
    position: relative;
}

.layoutColStyle {
    margin: 0px 5px;
    height: 40px;
    background-color: #EBEBEB;
    color: #4B5966;
    text-align: center;
    /* border-radius: 4px; */
}

