/* Tabs */

.react-tabs {
	-webkit-tap-highlight-color: transparent;
	display: flex;
	/* background: #f9f9f9; */
	min-height: 300px;
	padding: 0px;
  }

  .react-tabs__tab-list {
	border-right: 1px solid #ccc;
	border-bottom-width: 0px;
	margin: 0px;
	padding: 0;
	display: flex;
	flex-direction: column;
	width: 20%;
  }

  .react-tabs__tab {
	display: inline-block;
	border-bottom: 1px solid #ccc;
	bottom: -1px;
	position: relative;
	list-style: none;
	padding: 6px 12px;
	cursor: pointer;
	margin-bottom: 0px;
    padding: 14px;
  }

  .react-tabs__tab--selected {
    background: #ececec;
	border-width:1px;
	color: black;
	border-radius: 0;
  }

  .react-tabs__tab--disabled {
	color: GrayText;
	cursor: default;
  }

  .react-tabs__tab:focus {
	box-shadow: 0 0 5px hsl(208, 99%, 50%);
	border-color: hsl(208, 99%, 50%);
	outline: none;
  }

  .react-tabs__tab:focus:after {
	content: "";
	position: absolute;
	height: 5px;
	left: -4px;
	right: -4px;
	bottom: -5px;
	background: #fff;
  }

  .react-tabs__tab-panel {
	display: none;
	padding-left: 0px;
  }

  .react-tabs__tab-panel--selected {
	display: block;
	flex-grow:1;
  }
  .react-tabs__tab-panel--selected div:nth-child(odd) {
	background:#fff;
  }

  .react-tabs__tab-panel--selected div:nth-child(even) {

	background: #f9f9f9;
  }

  /* /Tabs */


